header.min-header {
  background-image: url("../images/header2-background.png");
  background-size: cover;
  background-position: bottom;
  padding-bottom: 225px;
  @include Sm {
    padding-bottom: 140px;
  }
  nav {
    .navbar-content {
      padding: 30px 0 30px;
    }
  }
  .carousel-news {
    background-color: #ffffff;
    width: 100%;
    border-radius: 100px;
    margin-bottom: 100px;
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    overflow: hidden;
    position: relative;
    @include Sm {
      display: none;
    }

    .title-news {
      position: absolute;
      right: 0;
      height: 100%;
      padding: 0 20px;
      display: flex;
      align-items: center;
      justify-content: center;
      z-index: 2;
      background-color: #a0c5c9;
      text-align: center;
      h3 {
        font-size: 15px;
        margin-bottom: 3px;
        font-family: "dinNext-regular";
        @include Sm {
          font-size: 13px;
        }
      }
    }
    p {
      margin-bottom: 3px;
      padding: 10px 0;
      text-align: center;
      font-family: "dinNext-regular";
      font-size: 15px;
      @include Sm {
        font-size: 10px;
      }
    }
    .owl-nav {
      background-color: #fff;
      height: 100%;
      padding: 0 20px;
      position: absolute;
      left: 0;
      top: 0;
      display: flex;
      align-items: flex-end;
      justify-content: center;
      text-align: center;
      button {
        transform: translateY(-50%);
        &.owl-next {
          margin-right: 10px;
        }
        img {
          filter: invert(1);
        }
      }
    }
  }
  .min-carousel {
    img {
      padding: 0 10px;
    }
    .owl-nav {
      @include Xs {
        display: none;
      }

      button {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        &.owl-prev {
          right: -50px;
        }
        &.owl-next {
          left: -50px;
        }
      }
    }
    .owl-dots {
      align-items: center;
      justify-content: center;
      margin-top: 70px;
      display: flex;
    }
    .owl-carousel button.owl-dot {
      margin-right: 6px;
      border-radius: 50%;
      padding: 4px !important;
      transition: 0.5s;
      background-color: #fff;
      transform: scale(0.8);

      &.active {
        transform: scale(1.6);
      }
    }

    .header-box {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      text-align: center;
      h4 {
        font-family: "dinNext-regular";
        font-size: 20px;
        color: #fff;
      }
      h2 {
        margin-top: 24px;
        margin-bottom: 24px;
        font-family: "Motken-noqta";
        color: #fff;
        font-size: 120px;
        @include mob480 {
          font-size: 90px;
        }
      }
      p {
        width: 75%;
        @include Xs {
          width: 100%;
        }
        color: #fff;
        font-size: 16px;
        line-height: 1.5;
        font-family: "dinNext-light";
        margin-bottom: 35px;
      }
      a {
        @include Button;
        background-color: #fff998;
        padding: 6px 40px;
      }
    }
  }
}
.services-section {
  background-image: url("../images/section2-background.png");
  background-size: cover;
  background-position: bottom;
  padding: 185px 0 85px;
  @media (min-width: 1920px) {
    background-size: 100% 100%;
  }
  // @include Sm {
  margin-top: -90px;
  // }

  .services-contnent {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 50px 0;
    flex-wrap: wrap;
    .services-box {
      color: #fff;
      width: calc(100% / 3);
      padding: 10px 50px;
      text-align: center;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      @include Xs {
        padding: 0 20px;
        width: calc(100% / 2);
      }
      @include mob600 {
        width: calc(100% / 1);

        margin-bottom: 40px;
      }
      h2 {
        font-size: 24px;
        font-family: "dinNext-bold";
        margin: 20px 0;
      }
      p {
        font-family: "dinNext-light";
        font-size: 14px;
        margin-bottom: 20px;
      }
      .info-box {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        padding: 10px 20px 30px;
        div {
          width: 40%;
          padding: 5px 10px;
          border-radius: 100px;
          background-color: #536e81;
          p {
            font-family: "dinNext-regular";
            font-size: 14px;
            margin-bottom: 0;
          }
        }
      }
      a {
        @include Button;
        background-color: #fff998;
        padding: 6px 40px;
        width: 150px;
        margin: auto;
      }
    }
  }
}
.registration-section {
  padding: 80px 0;
  @include Xs {
    padding: 60px 0;
  }

  .boxes-content {
    margin-top: 70px;
    .box {
      overflow: hidden;
      box-shadow: 0px 0px 38px 0px rgba(0, 0, 0, 0.13);
      color: #000;
      transition: 0.5s;
      background-color: #fff;
      border-radius: 12px;
      padding: 20px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      text-align: center;
      height: 270px;
      position: relative;
      margin-bottom: 20px;
      &::before {
        content: "";
        border-radius: 10px;
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: #68879c;
        z-index: 1;
        transition: 0.5s;
        transform: scale(0.01);
        opacity: 0;
      }
      .image-box {
        position: relative;
        z-index: 2;
        img {
          transition: 0.4s;
        }
      }
      h3 {
        position: relative;
        z-index: 2;
        margin-top: 40px;
        font-size: 16px;
        font-family: "dinNext-regular";
      }
      &:hover {
        color: #ffffff;
        &::before {
          transform: scale(1);
          opacity: 1;
        }

        img {
          filter: brightness(0) invert(1);
        }
      }
    }
  }
}
.statistics-section {
  padding: 80px 0 100px;
  @include Xs {
    padding: 60px 0;
  }

  .min-title {
    margin-bottom: 70px;
  }
  .counter-box {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin-bottom: 20px;
    h3 {
      font-family: "dinNext-regular";
      font-size: 73px;
    }
    .counter-title {
      margin-top: 20px;
      padding: 15px 10px;
      background-color: #fff;
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;
      box-shadow: 0px 1px 40px 0px rgba(0, 0, 0, 0.18);
      width: 100%;
      border-radius: 10px;
      position: relative;
      &::before {
        content: "";
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        top: -30px;
        align-self: center;
        border: 17px solid transparent;
        border-bottom-color: #fff;
      }
      p {
        margin-bottom: 0;
        font-size: 15px;
        color: rgb(13, 12, 12);
        font-family: "dinNext-regular";
      }
    }
  }
}
.setting-section {
  padding-bottom: 80px;
  background-image: url("../images/section3-background.png");
  background-size: 100% 100%;
  background-position: bottom;
  @include mob480 {
    padding-bottom: 60px;
  }
  margin-top: -40px;

  .options {
    display: flex;
    @include mob480 {
      align-items: center;
      justify-content: center;
    }
    ul {
      display: flex;
      margin-right: auto;
      @include mob480 {
        width: max-content;
        overflow-x: auto;
      }

      li {
        margin-right: 20px;
        border-radius: 100px;
        color: #000;
        @include mob480 {
          margin-bottom: 10px;
          width: max-content;
          flex-shrink: 0;
        }
        span {
          margin-right: 15px;
          font-family: "dinNext-regular";
        }
        display: flex;
        align-items: center;
        justify-content: center;
        &.gred {
          background-color: #fff998;
          padding: 5px 20px;
          border-radius: 100px;
          @include Sm {
            padding: 5px 10px;
            font-size: 10px;
          }
          @include mob480 {
            font-size: 17px;
          }

          cursor: pointer;
        }
        a {
          background-color: rgba(203, 210, 216, 0.471);
          padding: 5px 20px;
          border-radius: 100px;
          color: #000;
          transition: 0.5s;
          position: relative;
          @include Sm {
            padding: 5px 10px;
            font-size: 10px;
          }
          @include mob480 {
            font-size: 17px;
          }

          &::before {
            content: "";
            border-radius: 100px;
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background-color: #fff998;
            z-index: 1;
            transition: 0.5s;
            transform: scale(0.01);
            opacity: 0;
          }
          span {
            position: relative;
            z-index: 2;
          }
          img {
            position: relative;
            z-index: 2;
          }
        }
        &.active,
        &:hover {
          a {
            &::before {
              transform: scale(1);
              opacity: 1;
            }
          }
        }
      }
    }
  }
  .list-container {
    margin-top: 80px;
    @include mob480 {
      margin-top: 30px;
    }
    .list-item {
      background-color: #ffffff;
      border: 1px solid rgb(230, 236, 237);
      padding: 10px 15px;
      margin-bottom: 20px;
      .list-header {
        display: flex;
        // align-items: center;
        padding-bottom: 10px;
        border-bottom: 1px solid rgb(230, 236, 237);

        .count {
          border-radius: 4px;
          background-color: rgb(105, 132, 144);
          width: 35px;
          height: 35px;
          display: flex;
          align-items: center;
          justify-content: center;
          color: #ffffff;
          @include mob480 {
            width: 30px;
            height: 30px;
          }
        }
        p {
          margin-right: 15px;
          font-size: 20px;
          color: rgb(67, 67, 67);
          font-family: "dinNext-regular";
          margin-bottom: 0;
          @include mob480 {
            font-size: 17px;
            margin-right: 7px;
          }

          @include mob445 {
            font-size: 15px;
          }
        }
        ul {
          margin-right: auto;
          display: flex;
          button {
            background-color: transparent;
            border: none;
            margin-right: 5px;
          }
        }
      }
      .list-body {
        display: flex;
        padding: 20px 0;
        @include mob480 {
          flex-direction: column;
          padding: 20px 0 10px;
        }

        .colam {
          width: calc(100% / 3);
          display: flex;
          @include mob480 {
            width: calc(100% / 1);
            margin-bottom: 15px;
          }
          p {
            font-size: 16px;
            font-family: "dinNext-light";
            margin-bottom: 0;
            span {
              font-family: "dinNext-bold";
              margin-right: 2px;
            }
          }
          a {
            margin-right: auto;
            @include mob480 {
              margin-right: unset;
            }
            @include Button;
            background-color: #213d4f;
            color: #fff;
            &:hover::before {
              background-color: #245e81;
            }
          }
        }
      }
    }
  }
}
