﻿html {
  &::-webkit-scrollbar {
    width: 8px;
  }
  &::-webkit-scrollbar-track {
    background-color: $color-sec;
  }
  &::-webkit-scrollbar-thumb {
    // border-radius: 10px;
    background-color: $color-prim;
  }
  scrollbar-width: thin;
  scrollbar-color: rgba($color-prim, 1) rgba($color-sec, 1);

  overflow-x: hidden;
}

body {
  font-family: "frt", sans-serif;
  text-align: right;
}
.overflow {
  overflow: hidden;
}

* {
  font-family: inherit;
}

input,
a,
button {
  font-family: "frt", sans-serif;

  &:focus {
    outline: none;
    text-decoration: none;
  }

  &:hover {
    text-decoration: none;
  }
}

button {
  cursor: pointer;

  &:focus,
  &:active {
    outline: none;
    box-shadow: none;
  }
}

ul.res {
  padding: 0;
  margin: 0;
  list-style: none;
}
.min-title {
  margin: auto;
  text-align: center;
  position: relative;
  h2 {
    font-size: 40px;
    color: rgb(10, 10, 10);
    font-family: "dinNext-bold";
    margin-bottom: 27px;
  }
  p {
    font-family: "dinNext-light";
    font-size: 14px;
    margin-bottom: 0;
  }
  &::before {
    content: "";
    position: absolute;
    top: -60px;
    left: 50%;
    align-self: center;
    background-color: rgb(104, 135, 156);
    width: 2px;
    height: 30px;
  }
  &.wihte {
    h2 {
      color: #fff;
    }
    p {
      color: #fff;
    }
    &::before {
      background-color: #fff998;
    }
  }
}
