/*
  * all css code Here
*/

.nav-torger {
  display: none;
  @include Sm {
    display: flex;
  }
  z-index: 200;
  position: fixed;
  top: 50px;
  width: 55px;
  height: 55px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px;
  border-radius: 60px 20px / 100px;
  transition: 0.5s;
  cursor: pointer;
  border: unset;
  span {
    width: 28px;
    display: block;
    height: 3px;
    background-color: $color-sec;
    border-radius: 120px;
    transition: 0.5s;
    &:not(:last-of-type) {
      margin-bottom: 5px;
    }
  }
  &.active span {
    background-color: $color-prim;
    &:nth-child(1) {
      transform: translateY(8px) rotate(135deg);
    }
    &:nth-child(2) {
      transform: rotate(135deg);
      opacity: 0;
    }
    &:nth-child(3) {
      transform: translateY(-8px) rotate(-135deg);
    }
  }
}
.phone-list {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  height: 100%;
  width: 100%;
  align-items: center;
  justify-content: center;
  z-index: 100;
  visibility: hidden;
  .overlay {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba($color-sec, 0.5);
    opacity: 0;
    visibility: hidden;
    transition: 0.5s;
  }
  .navbar-list {
    position: relative;
    z-index: 110;
    background-color: $color-sec;
    height: 70%;
    width: 50%;
    @include mob425 {
      width: 90%;
    }
    padding: 40px 60px;
    border-radius: 60px 20px / 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: #fff;
    font-family: "dinNext-regular";
    opacity: 0;
    visibility: hidden;
    transition: 0.5s;
    transform: scale(0.01);
    li {
      font-size: 20px;
      a {
        color: #fff;
        font-family: "dinNext-regular";
        font-size: 20px;
      }
      &.drop {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        .collapse.in {
          display: flex;
          flex-direction: column;
        }
        li {
          margin-bottom: 3px;
        }
        a {
          font-size: 13px;
        }
      }
      margin-bottom: 10px;
    }
  }
  &.active {
    visibility: visible;
    .overlay {
      opacity: 1;
      visibility: visible;
    }
    .navbar-list {
      opacity: 1;
      visibility: visible;
      transform: scale(1);
    }
  }
}
.topbar {
  background-color: $color-sec;
  .content {
    padding: 12px 0;
    display: flex;
    align-items: center;
    color: $color-white;
    justify-content: space-between;
    @include Xs {
      justify-content: center;
    }

    ul {
      display: flex;
      li {
        display: flex;
        align-items: center;
        position: relative;
        p {
          margin-bottom: 0;
          color: #919fa8;
          font-family: "dinNext-light";
          margin-left: 18px;
          font-size: 15px;
        }
      }
    }
    &_right {
      ul li {
        &:first-of-type {
          padding-left: 30px;
          &::before {
            content: "";
            position: absolute;
            align-self: center;
            left: 0;
            width: 1px;
            height: 14px;
            background-color: $color-white;
          }
        }
        &:last-of-type {
          padding-right: 30px;
        }
      }
    }
    &_left {
      @include Xs {
        display: none;
      }

      ul li {
        &:not(:last-of-type) {
          margin-left: 27px;
        }
        a {
          transition: 0.6s;
          img {
            filter: invert(99%) sepia(92%) saturate(1%) hue-rotate(251deg)
              brightness(107%) contrast(100%);
            display: block;
            backface-visibility: hidden;
          }
          opacity: 0.5;
          &:hover {
            transform: translateY(-3px);
            opacity: 1;
          }
        }
      }
    }
  }
}
nav {
  .navbar-content {
    display: flex;
    align-items: center;
    padding: 30px 0 120px;
    @include Sm {
      flex-direction: column;
      padding: 30px 0 120px;
      .logo-box {
        margin-bottom: 30px;
      }
    }

    .navbar-list {
      @include Sm {
        display: none;
      }
      display: flex;
      margin-right: auto;
      align-items: center;
      margin-left: 40px;
      li {
        &:not(:last-of-type) {
          margin-left: 7px;
        }
        &.drop {
          align-items: center;
          justify-content: center;
          display: flex;
          position: relative;
          &::before {
            content: "";
            position: absolute;
            left: 7px;
            top: 50%;
            align-self: center;
            border: 5px solid transparent;
            border-top-color: #fff;
          }
          span {
            min-width: 88px;
            padding: 8px 20px;
            cursor: pointer;
            color: $color-white;
            font-family: "dinNext-regular";
            z-index: 2;
            position: relative;
            margin-bottom: 0;
            text-align: center;
          }
          ul {
            position: absolute;
            top: 45px;
            right: 0;
            transform: translateY(20px);
            opacity: 0;
            transition: 0.6s;
            width: 100%;
            background-color: #fff;
            border-radius: 10px;
            overflow: hidden;
            z-index: 10;
            visibility: hidden;
            li {
              margin-left: unset;
              overflow: hidden;
              a {
                display: flex;
                align-items: center;
                justify-content: center;
                padding: 5px 10px;
                font-family: "dinNext-regular";
                color: $color-sec;
                width: 100%;
                transition: 0.4s;
                position: relative;
                &::before {
                  content: "";
                  border-radius: 100px;
                  position: absolute;
                  width: 100%;
                  height: 100%;
                  background-color: rgba(33, 61, 79, 0.38);
                  z-index: 1;
                  transition: 0.5s;
                  transform: scale(0.01);
                  opacity: 0;
                }

                &:hover::before {
                  border-radius: 0;
                  transform: scale(1);
                  opacity: 1;
                }
              }
              &:not(:last-of-type) {
                border-bottom: 1px solid rgba(33, 61, 79, 0.38);
              }
            }
          }
          &:hover ul {
            transform: translateY(0);
            opacity: 1;
            visibility: visible;
          }
        }

        &.list a {
          padding: 8px 20px;
          font-size: 15px;
          transition: 0.5s;
          position: relative;
          display: flex;
          align-items: center;
          justify-content: center;
          min-width: 88px;

          span {
            color: $color-white;
            font-family: "dinNext-regular";
            z-index: 2;
            position: relative;
            margin-bottom: 0;
          }

          &::before {
            content: "";
            border-radius: 100px;
            position: absolute;
            width: 100%;
            height: 100%;
            background-color: rgba(33, 61, 79, 0.38);
            z-index: 1;
            transition: 0.6s;
            transform: scale(0.01);
            opacity: 0;
          }
        }
        &.list.active > a span {
          color: rgb(255, 249, 152);
        }
        &.list.active > a::before {
          transform: scale(1);
          opacity: 1;
        }
        &.list:hover > a span {
          color: rgb(255, 249, 152);
        }
        &.list:hover > a::before {
          transform: translateY(0);
          opacity: 1;
        }
      }
    }
    .button-container {
      display: flex;
      a {
        @include Button;
        background-color: $color-white;
        &:first-of-type {
          background-color: #fff998;
          margin-left: 12px;
        }
      }
    }
  }
}
header.sec-header {
  background-image: url("../images/header-background.png");
  background-size: 100% 100%;
  @include mob600 {
    background-size: cover;
  }

  background-repeat: no-repeat;
  background-position: bottom;
  padding-bottom: 225px;
  @include Sm {
    padding-bottom: 30px;
  }
  @include Xs {
    padding-bottom: 15px;
  }
}
.navigator {
  display: flex;
  position: relative;
  margin: 0;
  list-style: none;
  padding: 0;
  @include mob480 {
    margin-top: 20px;
  }

  &::before {
    content: "";
    position: absolute;
    right: 0;
    top: 4px;
    align-self: center;
    width: 2px;
    height: 20px;
    background-color: rgb(255, 249, 152);
  }
  li {
    position: relative;
    display: flex;
    align-items: center;
    padding-right: 15px;
    justify-content: center;
    &:not(:last-of-type) {
      &::before {
        content: url("../images/left-arrow1.png");
        position: absolute;
        align-self: center;
        left: -16px;
        top: 50%;
        transform: translateY(-30%);
      }
    }
    a {
      color: #fff;
      font-family: "dinNext-regular";
    }
  }
}
.videos-section {
  padding: 80px 0;
  position: relative;
  background-image: url("../images/section1-background.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  @media (min-width: 1920px) {
    background-size: 100% 100%;
  }
  margin-top: -90px;
  .pattern-1 {
    position: absolute;
    bottom: 0;
    left: 0;
    @include Xs {
      display: none;
    }
  }
  .navigator {
    margin-bottom: 60px;
  }
  .videos-content {
    margin-bottom: 130px;
    background-color: rgba(255, 255, 255, 0.18);
    border-radius: 12px;
    padding: 10px 13px;
    @include Sm {
      margin-top: 20px;
      margin-bottom: 100px;
    }
    @include mob480 {
      margin-bottom: 40px;
    }
  }
  .videos-list {
    max-height: 602px;
    overflow-y: scroll;
    padding: 0 13px;

    &::-webkit-scrollbar {
      width: 8px;
    }
    &::-webkit-scrollbar-track {
      background-color: #cbdce8;
      border-radius: 10px;
    }
    &::-webkit-scrollbar-thumb {
      background-color: $color-prim;
      border-radius: 10px;
    }
    scrollbar-width: thin;
    scrollbar-color: #cbdce8 rgba($color-sec, 1);

    .videos-box {
      display: flex;
      padding: 23px 0 14px;
      cursor: pointer;
      &:not(:last-of-type) {
        border-bottom: 1px solid #aebfca;
      }

      .imgae-box {
        width: 78px;
        height: 63px;
        border-radius: 15px;

        img {
          width: 100%;
          height: 100%;
        }
      }
      .content {
        padding-right: 10px;
        h3 {
          font-size: 14px;
          color: #fff;
          font-family: "dinNext-bold";
          margin-bottom: 12px;
        }
        p {
          font-size: 14px;
          color: #c0cad1;
          font-family: "dinNext-light";
          margin-bottom: 0;
        }
      }
    }
  }
  .scrollBar {
    padding: 10px 0;
    border-top: 1px solid #aebfca;
    border-bottom-left-radius: 12px;
    border-bottom-right-radius: 12px;
    display: flex;
    align-items: center;
    justify-content: center;

    a {
      transition: 0.5s;
      &:hover {
        transform: translateY(3px);
      }
    }
  }
  .video-lg {
    // position: relative;
    .video-icon {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, 20%);
    }
  }
  .video-title {
    h2 {
      font-size: 30px;
      color: #fff;
      font-family: "dinNext-bold";
      @include mob480 {
        font-size: 25px;
      }
    }
    p {
      font-size: 14px;
      color: #c0cad1;
      font-family: "dinNext-light";
      margin-bottom: 30px;
    }
  }
  .show-video {
    border-radius: 15px;
  }
}
.assemblies-section {
  padding: 80px 0;
  background-image: url("../images/assem-background.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: bottom;
  @include mob600 {
    padding: 40px 0;
  }
  @include mob425 {
    padding: 20px 0 0;
  }
  .carousel-container {
    padding-top: 80px;
    @include mob600 {
      padding-top: 40px;
    }
    .owl-dots {
      align-items: center;
      justify-content: center;
      margin-top: 20px;
      display: none;
      @include Xs {
        display: flex;
      }
    }
    .owl-carousel button.owl-dot {
      @include Xs {
        margin-right: 6px;
        border-radius: 50%;
        padding: 4px !important;
        transition: 0.5s;
        background-color: $color-prim;
        transform: scale(0.7);
      }

      &.active {
        background-color: $color-sec;
        transform: scale(1);
      }
    }
    .slide-card {
      border-radius: 12px;
      box-shadow: 0px 0px 18px 0px rgba(0, 0, 0, 0.1);
      margin: 10px;
      padding: 40px 20px;
      display: flex;
      flex-direction: column;
      color: rgb(10, 10, 10);
      transition: 0.6s;
      position: relative;
      .content {
        position: relative;
        z-index: 3;
        h3 {
          font-size: 18px;
          font-family: "dinNext-bold";
        }
        p {
          font-size: 18px;
          font-family: "dinNext-regular";
        }
      }
      .image-box {
        height: 90px;
        width: 63px;
        align-self: center;
        position: relative;
        z-index: 3;
      }
      &::before {
        content: "";
        border-radius: 12px;
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(200, 200, 200, 0.38);
        z-index: 1;
        transition: 0.5s;
        transform: scale(0.01);
        opacity: 0;
      }
      &:hover::before {
        transform: scale(1);
        opacity: 1;
      }
    }
    .owl-nav {
      @include Xs {
        display: none;
      }

      button {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        &.owl-prev {
          right: -50px;
        }
        &.owl-next {
          left: -50px;
        }
        img {
          filter: invert(1);
        }
      }
    }
  }
  .info-section {
    padding: 80px 0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    @include Xs {
      padding: 40px 0 0;
    }

    .box {
      color: #000;
      width: calc(100% / 3);
      padding: 10px 50px;
      @include Xs {
        padding: 0 20px;
      }
      @include Sm {
        width: calc(100% / 2);
      }
      @include mob375 {
        width: calc(100% / 1);
        margin-bottom: 40px;
      }
      .image-box {
        padding: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: #68879c;
        border-radius: 10px;
        position: relative;
        &::before {
          content: "";
          border-radius: 10px;
          position: absolute;
          width: 100%;
          height: 100%;
          background-color: rgba(33, 61, 79, 0.38);
          z-index: 1;
          transition: 0.5s;
          transform: scale(0.01);
          opacity: 0;
        }
        img {
          position: relative;
          z-index: 2;
          display: block;
        }
      }
      &:hover .image-box::before {
        transform: scale(1);
        opacity: 1;
      }

      h2 {
        text-align: center;
        font-family: "dinNext-bold";
        font-size: 30px;
        @include Xs {
          font-size: 20px;
        }

        margin-top: 20px;
      }
    }
  }
}
footer {
  padding: 80px 0;
  background-image: url("../images/footer-background.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  position: relative;
  @include Sm {
    // background-size: contain;
    background-image: linear-gradient(to top, $color-prim, transparent),
      url("../images/footer-background.png");
    padding: 80px 0 20px;
  }
  a {
    color: #fff;
  }
  .toTop {
    position: absolute;
    top: 100px;
    @include Sm {
      top: 40px;
    }
    transition: 0.5s;
    &:hover {
      transform: translateY(-3px);
    }
    img {
      width: 60px;
    }
  }
  .footer-top {
    margin-top: 14%;
    @include mob480 {
      text-align: center;
    }

    .about {
      img {
        @include mob480 {
          width: 60%;
        }
      }
      h3 {
        font-family: "dinNext-regular";
        font-size: 17px;
        margin-bottom: 25px;
      }
      @include Sm {
        margin-bottom: 40px;
      }
    }
    h3 {
      font-size: 19px;
      font-family: "dinNext-regular";
      color: #fff;
      margin-bottom: 25px;
    }
    ul {
      li {
        transition: 0.5s;
        &:hover {
          transform: translateX(-3px);
          a {
            color: #fff;
          }
        }
      }
    }
    .imageCr {
      display: flex;
      align-items: center;
      justify-content: center;
      img {
        @include Xs {
          width: 80%;
        }
      }
    }

    .footer-nav {
      list-style: "-";
      @include mob480 {
        list-style: none;
        margin-bottom: 27px;
      }

      color: #fff;
      li {
        margin-bottom: 10px;
        padding: 0 10px 0;
        a {
          font-family: "dinNext-light";
        }
      }
    }
    .footer-info {
      @include mob480 {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
      }

      li {
        margin-bottom: 10px;
        a {
          display: flex;
          align-items: center;
          font-size: 14px;
          span {
            margin-right: 14px;
          }
        }
      }
      .address {
        margin-top: 40px;
        display: flex;
        align-items: flex-start;
        @include Xs {
          margin-top: 20px;
          margin-bottom: 20px;
          align-items: center;
        }

        p {
          margin-right: 14px;
          font-family: "dinNext-regular";
          color: #fff;
          font-size: 14px;
          margin-bottom: 0;
        }
      }
    }
  }
}
.copyright-section {
  background-color: $color-sec;
  padding: 20px 0;
  .content {
    display: flex;
    align-items: center;
    @include mob480 {
      flex-direction: column-reverse;
      justify-content: center;
    }

    p {
      margin-bottom: 0;
      font-size: 13px;
      color: #fff;
      font-family: "dinNext-regular";
    }
    ul {
      margin-right: auto;
      margin-left: 20%;
      display: flex;
      @include mob480 {
        margin-left: unset;
        margin: 20px auto 10px;
      }
      @include Sm {
        margin: 20px auto;
      }
      li {
        &:not(:last-of-type) {
          margin-left: 27px;
          @include Sm {
            margin-left: 15px;
          }
        }
        a {
          transition: 0.6s;
          display: block;
          img {
            filter: invert(99%) sepia(92%) saturate(1%) hue-rotate(251deg)
              brightness(107%) contrast(100%);
            display: block;
            backface-visibility: hidden;
          }
          opacity: 0.5;
          &:hover {
            transform: translateY(-3px);
            opacity: 1;
          }
        }
      }
    }
  }
}
