@mixin Flex {
  display: flex;
  align-items: center;
  justify-content: center;
}
@mixin Button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 4px 18px;
  border-radius: 100px;
  color: #213d4f;
  font-size: 13px;
  font-family: "dinNext-regular";
  position: relative;
  overflow: hidden;
  span {
    position: relative;
    z-index: 2;
    transition: 0.5s;
  }
  &::before {
    content: "";
    border-radius: 100px;
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(33, 61, 79, 0.38);
    z-index: 1;
    transition: 0.5s;
    transform: scale(0.01);
    opacity: 0;
  }
  &:hover::before {
    transform: scale(1);
    opacity: 1;
  }
  &:hover span {
    color: #fff;
  }
}
